import Data from "@/js/core/data";
import client from "../core/client";
const Feedback_Add_URL = "https://api.${region}.500apps.com/feedbackup/";

class Feedback extends Data {
  // Get project details
  getProject() {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/projects`);
    data.disablePCors();
    return data.get();
  }
  // Get details based on id and table
  getFeedbackDetails(id) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/${id}/feedback-details`);
    data.disablePCors();
    return data.get();
  }
  // Add feedback
  addFeedback(form) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/feedback`);
    data.disablePCors();
    return data.multi_call(form);
  }

  // Update feedback
  updateFeedback(form) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/${form.id}/feedback`);
    data.disablePCors();
    return data.edit(form.id, form);
  }

  // Get feedback details count
  async getFeedbackCount(params) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/feedback`);
    data.params = params ? params + "&count=true" : "count=true";
    data.limit = `limit 5`;
    data.disablePCors();
    return data.get();
  }

  // Get details based on id and table
  getDetails(id, table) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/${table}`);
    data.params = `id=${id}`;
    data.limit ="limit 1";
    data.disablePCors();
    return data.get();
  }

  // Get filtered data for conditional and sorted data using this method
  async getFilterData(where, order_by = "", offset = 0) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/feedback`);
    data.params = where ? (order_by ? `${where}&${order_by}` : `${where}`) : order_by ? `${order_by}` : "";
    // limit and offset added
    data.limit = `limit 200 offset ${offset}`;
    data.disablePCors();
    return data.get();
  }

  // Get feedback
  getFeedback(offset) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/feedback`);
    // limit and offset added
    data.limit = `limit 200 offset ${offset}`;
    data.disablePCors();
    return data.get();
  }

  // Get top categories
  getTopFeedbacks() {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/top-categories`);
    data.disablePCors();
    return data.get();
  }

  // Get top categories
  getTopRatedProjects() {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/top-projects`);
    data.disablePCors();
    return data.get();
  }

  // Visitor Update
  async updateVisitor(form, id) {
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/visitor/${id}`);
    data.params = `domain_id=${window.domain}`;
    data.disablePCors();
    let response = await data.edit(id, form);
    this.setCookie("fname", form.name, 90);
    return response;
  }

  // Visitor Info
  async addVisitor(form) {
    this.setCookie("fname", form.name, 90);
    let browserData = client.getVisitorInfo();
    form.domain_id = window.domain;
    form.browser = browserData.browser;
    form.device = browserData.deviceType;
    form.os = browserData.OS;
    form.resolution = window.screen.width + " * " + window.screen.height;
    let data = new Data(`${Feedback_Add_URL}${window.domain}/${window.project_id}/visitor`);
    data.disablePCors();
    let response = await data.multi_call(form);
    this.setCookie("FVID", response[0].GENERATED_KEY, 90);
    return response;
  }
  setCookie(cName, cValue, expDays) {
    let date = new Date();
    date.setTime(date.getTime() + expDays * 24 * 60 * 60 * 1000);
    var expires = "expires=" + date.toUTCString();
    document.cookie = cName + "=" + cValue + "; " + expires + "; path=/";
  }
}

export default new Feedback();
