<template>
  <div>
    <b-modal id="visitor" hide-footer hide-header no-close-on-backdrop>
      <b-form @submit.prevent="onSubmit" ref="form">
        <h3>Enter your details</h3>
        <b-form-group label="Name" class="mb-0">
          <b-form-input v-model="visitor_form.name" type="text" placeholder="Enter Name" autocomplete="on" required></b-form-input>
          <span class="mb-0 text-muted small-2 float-right">{{ visitor_form.name.length }}/50</span>
        </b-form-group>
        <b-form-group label="Email">
          <b-form-input v-model="visitor_form.email" type="email" placeholder="Enter Email" required autocomplete="on"></b-form-input>
        </b-form-group>
        <b-button type="submit" variant="primary" class="float-right px-4 btn-sm">Save</b-button>
        <b-button variant="secondary" @click="skip()" class="float-right px-4 btn-sm mr-2 ml-2">Skip</b-button>
      </b-form>
    </b-modal>
    <div class="bg-white fixed-bottom p-4">
      <div>
        <QuillEditor ref="myQuillEditor" :value="form.description" />
      </div>
      <div class="mt-4 feedbackup-savebutton">
        <b-button @click="saveComment(visitor_id)" variant="primary" size="sm" class="float-right px-4 " v-if="!disabled">{{ btn_save }}</b-button>
        <b-button @click="saveComment(visitor_id)" variant="primary" size="sm" class="float-right px-4" disabled v-if="disabled">{{ btn_save }}</b-button>
      </div>
      <p v-if="error_show" :class="error_cls + ' float-right mr-3 mt-2 position-absolute z-index-99 feedback-validation badge'">{{ error_msg }}</p>
    </div>
  </div>
</template>
<script>
  import Comments from "@/js/feedbackup/comments";
  import QuillEditor from "./QuillEditor.vue";
  // require styles
  import { bus } from "@/js/core/emit-bus";
  import FeedbackUp from "@/js/feedbackup/feedbackup";

  export default {
    components: { QuillEditor },
    props: {
      feedbackData: Object,
      visitor_id: String,
    },
    data() {
      return {
        visitor_form: {
          name: "",
          email: "",
        },
        form: {
          description: "",
          commented_by: this.visitor_id,
          domain_id: window.domain,
          created_by: window.user, // Its need for activity
          feedback_id: this.feedbackData.id,
        },
        error_show: false,
        error_cls: "text-success",
        error_msg: "",
        btn_save: "Save",
        disabled: false,
        edit_error_show: false,
        edit_error_cls: "text-success",
        edit_error_msg: "",
        edit_btn_update: "Update",
        edit_index: 0,
        edit_form: {},
      };
    },
    mounted() {},
    methods: {
      skip() {
        this.onSubmit();
      },
      async onSubmit() {
        let response = await FeedbackUp.addVisitor(this.visitor_form);
        this.$bvModal.hide("visitor");
        if (response) {
          this.$parent.$parent.$parent.visitor_id = response[0].GENERATED_KEY;
          this.$parent.$parent.$parent.visitor_name = this.visitor_form.name ? this.visitor_form.name : "";
        }
        this.form.commented_by = response[0].GENERATED_KEY;
        this.saveComment(response[0].GENERATED_KEY);
      },
      async saveComment(key) {
        if ((this.$refs.myQuillEditor.description.replace(/( |<([^>]+)>)/gi, "").trim() == "") & !this.$refs.myQuillEditor.description.includes("<img")) {
          this.error_show = true;
          this.error_cls = "text-danger badge-soft-danger";
          this.error_msg = "Please add comment.";
          setTimeout(() => {
            this.error_show = false;
            this.error_cls = "";
          }, 1500);
          return;
        }
        this.disabled = true;
        if (key == "0") {
          this.$bvModal.show("visitor");
        } else {
          this.btn_save = "Saving";
          this.form.description = this.$refs.myQuillEditor.description;
          let response = await Comments.addComment(this.form);
          this.btn_save = "Saved";
          this.error_show = true;
          this.error_cls = "text-success badge-soft-success";
          this.error_msg = "Comment added successfully!!.";
          setTimeout(() => {
            this.error_show = false;
            this.error_cls = "";
          }, 1500);
          //   let insertData = await Comments.getActivityDetails(response[0].ACTIVITY_ID, this.feedbackData.id);
          let insertData = await Comments.getActivityDetails(response[0].ACTIVITY_ID, this.feedbackData.id);
          this.btn_save = "Save";
          this.disabled = false;
          bus.$emit("submitComment", insertData);
          this.form.description = "";
          this.$refs.myQuillEditor.description = "";
        }
      },
    },
  };
</script>
