import Data from "@/js/core/data";
const Comments_URL = "https://api.${region}.500apps.com/feedbackup/";

class Comments extends Data {
  constructor() {
    super(Comments_URL);
    this.disablePCors();
  }
  // Get details based on id and table
  getDetails(id, feedback_id) {
    let data = new Data(`${Comments_URL}${window.domain}/${window.project_id}/${feedback_id}/comments`);
    data.params = `id=${id}`;
    data.disablePCors();
    return data.get();
  }

  getActivityDetails(id, feedback_id) {
    let data = new Data(`${Comments_URL}${window.domain}/${window.project_id}/${feedback_id}/feedback-details`);
    data.params = `id=${id}`;
    data.disablePCors();
    return data.get();
  }

  // Get comments
  getComments(feedback_id) {
    let data = new Data(`${Comments_URL}${window.domain}/${window.project_id}/${feedback_id}/comments?feedback_id=${feedback_id}`);
    data.disablePCors();
    return data.get();
  }

  // Add comment for feedback
  addComment(form) {
    let data = new Data(`${Comments_URL}${window.domain}/${window.project_id}/${form.feedback_id}/comments`);
    data.disablePCors();
    return data.multi_call(form);
  }

  // Update comment for feedback
  updateComment(form) {
    let data = new Data(`${Comments_URL}${window.domain}/${window.project_id}/${form.feedback_id}/${form.id}/comments`);
    data.disablePCors();
    return data.edit(form.id, form);
  }
}

export default new Comments();
